#header_section{
    width: 100%; height: 194px; background-color: rgba(0,0,0,0.6);
    position: absolute; z-index: 2;
    background: linear-gradient(to bottom, rgba(29, 29, 31, 1) 0%, rgba(0, 0, 0, 0) 100%);

    h1 {
        position: absolute; z-index: 1; top: 18px; left: 50%;
        transform: translateX(-50%); height: 36px;
        img{ width: 100%; height: 100%; object-fit: contain;} 
    }

    .gnb{
        position: relative; width: 980px; height: 70px; margin: 0 auto;
        border-bottom: 1px solid #b2b2b2;
        .g_menu{ 
            position: absolute; display: flex; gap: 14px; opacity: 0.4;
            font-size: 12px;
            a{ color: #fff; }
        }
        .g_menu1{ 
            left: 0; top: 40px;
            a{
                padding-left: 18px; display: block; height: 14px; 
                background: url('../public/images/gnb_facebook_wht.png') no-repeat;
                background-position: 0 2px;
            }
            .youtube{ background-image: url('../public/images/gnb_youtube_wht.png');}
            .insta{ background-image: url('../public/images/gnb_follow_wht.png');}
        }
        .g_menu2{ right: 0; top: 40px; }
        .g_menu3{ 
            right: 0; top: 88px; opacity: 1;
            a{
                padding-left: 18px; display: block; height: 14px; 
                background: url('../public/images/nav_side_my_wht.png') no-repeat;
                background-position: 0 2px;
            }
            .direct{ background-image: url('../public/images/nav_side_reserve_wht.png');}
            .btn_allMenu{
                background: url('../public/images/nav_side_all_wht.png') no-repeat center;
                width: 17px; border: 0 none; text-indent: -9999em; cursor: pointer;
                margin-top: -2px;
            }
        }
    }

    #nav{
        width: 980px; margin: 8px auto 0; text-align: center;
        ul{
            li{ display: inline-block; vertical-align: top;}
            a{
                display: block; height: 40px; line-height: 40px; padding: 0 24px; 
                font-size: 14px; letter-spacing: 0.1em; white-space: nowrap;
                font-weight: bold; color: #fff;
            }
            .on:hover{ border-bottom: 1px solid red;}
            .sub_menu{
                display: none; background-color: rgba(0,0,0,.7);
                position: absolute; left: 0; right: 0; top: 119px;
                height: 42px; text-align: center; z-index: 2;
                ul{
                    display: flex; justify-content: center; opacity: 0;
                    a{ line-height: 42px; }
                }
            }
            .on:hover>.sub_menu{ display: block;}
            .on:hover>.sub_menu ul{ opacity: 1; z-index: 3;}
        }
    }

    #allmenu{
        .allmenu_inner{
            position: fixed; left: 50%; top: 70px; transform: translate(-50%); 
            width: 980px; height: 645px; background: #fff; padding: 28px 0 0;
            display: flex; flex-direction: column; justify-content: space-between;
            box-sizing: border-box; font-weight: bold;
            .close{
                position: absolute; right: -44px; top: 0; height: auto; cursor: pointer;
                img{
                    width: 100%; height: 100%; object-fit: cover;  
                }
            }
            .allmenu_top{
                display: flex; justify-content: space-between; padding: 0 20px;
                .main{
                    table{
                        caption{ display: none; }
                        tbody{
                            tr{
                                width: 620px; white-space: nowrap;
                                th{ 
                                    padding: 0 5px; font-size: 14px;
                                    span{
                                        display: inline-block; border-bottom: 1px solid #707070;
                                    }
                                }
                                td{
                                    vertical-align: top; padding: 0 5px 30px; font-size: 11px;
                                    ul{
                                        display: flex; flex-direction: column; margin-top: 8px; gap: 4px;
                                        li{
                                            a{ display: inline-block; color: #151515; padding-left: 0;
                                                &:hover{ color: #fc0; }
                                            }
                                            
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                .user{
                    display: flex; flex-direction: column; justify-content: space-between;
                    align-items: center;
                    height: 356px; border-left: 1px solid rgba(112, 112, 112, .1);
                    padding-left: 10px;
                    .allmenu_login{ 
                        border: 1px solid #151515; text-align: center;
                        border-radius: 5px; color: #151515; font-weight: bold;
                        font-size: 14px; width: 84%; height: 33px; line-height: 33px;
                        padding-left: 0;
                    }
                    .allmenu_signup{
                        border-radius: 10px; padding: 20px; width: 84%; background: #ededed;
                        display: flex; flex-direction: column; gap: 24px; align-items: center;
                        p{
                            text-align: center; line-height: 1.4rem; margin: 0; padding: 0;
                            font-size: 13px; color: #666;
                        }
                        a{ 
                            color:#151515; font-weight: bold; display: block; text-align: center;
                            border: 1px solid #151515; border-radius: 52px; background: #fff;
                            width: 40%; height: 33px; line-height: 33px; text-align: center;
                            padding-left: 0;
                        }
                    }
                    .banner{ 
                        display: block; height: 125px; width: 287px; padding-left: 0; 
                    }
                }
            }
            .ad{
                a{ 
                    width: 100%; height: 177px; padding-left: 0;
                    img{ width: 100%; height: auto; object-fit: cover; border-radius: 0 0 5px 5px;}
                }
            }
        }
    }
}
#main_content{
    .Mainvisual{ 
        width: 100%; height: 774px; z-index: 0; position: relative;
        overflow: hidden; background-color: #000;
        .inner{
            position: absolute; left: 50%; top: 0; width: 1920px;
            margin-left: -960px;
            .mySwiper{
                width:1920px; height: 744px; 
                .SwiperSlide{
                    width: calc(1920px * 3); height: 100%; display: flex; 
                    position: relative; 
                    .effect{
                        width: 100%; height: 100%; position: relative;
                        &::after{
                            content: ""; display: block; height: 140px; width: 100%;
                            position: absolute; left: 0; right: 0; bottom: 0;
                            background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 85%, rgba(0, 0, 0, 1) 100%); 
                            z-index: 100;
                        }
                    }
                    .start_btn{
                        &::after{
                            content: ""; position: absolute; left: 50%; top: 50%; 
                            display: block; width: 49px; height: 54px; z-index: 200;
                            background: url('../public/images/btn_main_visual_play.png') no-repeat;
                        }
                    }
                    img{
                        display: block; width: 1920px; height: 774px; object-fit: cover;
                    }
                }
            }
            .swiper-button-prev, .swiper-button-next{ 
                background: url('../public/images/arr_lf_31_wht.png') no-repeat;
                position: absolute; top: 50%; width: 31px; height: 60px;
                z-index: 20; cursor: pointer;
            }
            .swiper-button-prev{ left: 20%;}
            .swiper-button-next{ 
                background-image: url('../public/images/arr_rg_31_wht.png');
                right: 20%;
            }
            .swiper-pagination{ 
                position: absolute; right: 20%; left: auto; top: auto; bottom: 85px; 
                z-index: 200; display: flex; gap: 10px;
            }
            .swiper-pagination-bullet{ 
                background-color: rgba(0,0,0,0.6); color: inherit; 
                width: 12px; height: 12px; display: inline-block;
                border-radius: 50%;
            }
            .swiper-pagination-bullet-active {background-color: #000;}
        }
    }
    
    
    #contents{
        width: 100%; max-width: 980px; margin: 0 auto; padding: 50px 0 0 0;
        .title{
            padding-bottom: 10px; margin-bottom: 10px;
            h2{ font-size: 17px; }
            }
        .now_movies{
            margin: -100px 0 70px; position: static;
            .movies_wrap{
                padding-top: 50px; display: flex; flex-flow: row wrap;
                justify-content: space-between; gap: 15px;
                .movie{
                    flex: 1 1 0; text-align: center; color: #fff;
                    .top_info{
                        position: relative; height: 262px; border-radius: 4px;
                        .poster_info{
                            height: 262px;
                            img{ width: 100%; height: auto; object-fit: cover; }
                            strong{
                                position: absolute; left: 0; right: 0; bottom: 0;
                                height: 23px; padding-top: 10px; color: #fff;
                                font-weight: bold; font-size: 26px; font-style: italic;
                                text-align: left;
                            }
                        }
                        .over_box{
                            overflow: hidden; position: absolute; left: 0; top: 0; width: 100%; height: 0;
                            .inner{
                                display: flex; flex-direction: column; gap: 14px;
                                margin-top: 74px;
                                a{
                                    display: block; color: #fff;
                                    line-height: 26px; height: 28px; text-align: center;
                                    padding: 0 14px; cursor: pointer; box-sizing: border-box;
                                    border-radius: 4px; border: 1px solid #fff;
                                }

                            }
                        }
                    }
                    &:hover .over_box{
                        position: absolute; top: 0; left: 0; padding: 0 18px;
                        width: 100%; height: 100%; z-index: 1; box-sizing: border-box;                 background: rgba($color: #000, $alpha: .5);
                    }
                    .btm_info{
                        margin-top: 20px;
                        .title_info{
                            display: block; overflow: hidden; white-space: nowrap;
                            text-overflow: ellipsis; margin-bottom: 6px; font-size: 13px;
                            font-weight: bold;
                            .grade{ 
                                padding-right: 6px; display: inline-block; width: 22px; height: 22px;
                                line-height: 22px; border-radius: 50%; vertical-align: middle;
                                text-align: center;
                            }
                        }
                        .sub_info{
                            display: block; margin-bottom: 10px; font-size: 11px; font-weight: bold;
                            span{ position: relative; margin-left: 8px;}
                            .rate_info{ margin-left: 0;}
                            .remain_info{ padding-left: 10px; color: #ED4C6B;}
                            .star_info{ 
                                padding-left: 22px; background: url('../public/images/star.png') no-repeat 9px 20%; 
                            }
                            span:nth-child(2)::before{
                                content: ""; display: inline-block; position: absolute; top: 2px; left: 0; width: 1px; height: 8px; background-color: #b2b2b2;
                            }
                        }
                    }
                }
            }
            &::before{
                content: ""; position: absolute; display: block; background: #000;
                left: 0; right: 0; width: 100%; height: 405px; z-index: -1;
            }
        }
        .special{
            margin-bottom: 34px; position: relative;
            .title{ 
                border-bottom: 1px dashed #d9d9d9; padding-bottom: 10px;  
            }
            .sp_wrap{
                display: flex; justify-content: space-between; padding: 16px 0;
            }
            .more{ 
                position: absolute; right: 0; top: 5px;
                background: url('../public/images/arr_rg_6.png') no-repeat right center;
                border: 0 none; width: auto; padding: 0 10px 0 0; color: #666; font-size: 14px;
            }
        }
        .evt{
            margin-bottom: 44px; position: relative;
            .evt_wrap{
                height: 512px; display: flex; flex-flow: row wrap; gap: 11px;
                position: relative;
                li{
                    height: 250px;
                }
                .e1{ width: 183px; }
                .e2{ width: 183px; }
                .e3{ width: 286px; }
                .e4{ width: 295px; height: 511px; position: absolute; right: 0; top: 0;}
                .e5{ width: 478px; }
                .e6{ width: 183px; }
            }
            .more{ 
                position: absolute; right: 0; top: 5px;
                background: url('../public/images/arr_rg_6.png') no-repeat right center;
                border: 0 none; width: auto; padding: 0 10px 0 0; color: #666; font-size: 14px;
            }
        }
        .Premiere{
            margin-bottom: 34px; position: relative;
            .premiere_wrap{ 
                display: flex; justify-content: space-between;
                img{ border-radius: 5px; } 
            }
            .more{ 
                position: absolute; right: 0; top: 5px;
                background: url('../public/images/arr_rg_6.png') no-repeat right center;
                border: 0 none; width: auto; padding: 0 10px 0 0; color: #666; font-size: 14px;
            }
        }
        .direct_btns{
            margin-bottom: 30px; display: flex; justify-content: space-between;
        }
        .Notice{
            padding-bottom: 10px; border-bottom: 1px solid #aaa; margin: 0;
            position: relative;
            .title{
                display: inline-block; margin: 0; font-size: 14px; vertical-align: middle;
                padding-bottom: 0;
                &::after{
                    content: ""; display: inline-block; width: 1px; height: 13px;
                    background: #707070; margin: 0 20px; vertical-align: middle;
                }
            }
            .rolling_menu_wrap{
                position: relative; display: inline-block; width: 840px; height: 19px;
                overflow: hidden; font-size: 14px; vertical-align: middle;
                .rolling_menu{ 
                    position: absolute; left: 0;
                    a{ color: #151515; font-size: 14px; font-weight: bold;}
                    @keyframes rolling {
                        0%{ top: 0;}
                        100%{ top: -20px; }
                    }
                    animation: rolling 2s ease infinite;
                }
            }
            .more{ 
                position: absolute; right: 0; top: 5px;
                background: url('../public/images/arr_rg_6.png') no-repeat right center;
                border: 0 none; width: auto; padding: 0 10px 0 0; color: #666; font-size: 14px;
            }
        }
    }
}
#evt_box{
    padding-top: 100px; display: flex; justify-content: center;
}
.Banner_section{
    padding-top: 70px; text-align: center;
    .banner_1{ margin-bottom: 50px;}
    a{ display: block; width: 980px; margin: 0 auto;}
}
.Footer{
    padding-top: 80px;
    .inner{
        position: relative; width: 980px; margin: 0 auto;
        .main_footer{
            .f_logo{ display: block; margin-bottom: 20px; }
            .txt_underline{ color: #151515; font-weight: bold; text-decoration: underline;}
            .f_menu{ 
                margin-bottom: 15px; display: flex; 
                li{
                    position: relative; margin-right: 10px;
                    &::before{
                        content: ""; position: absolute; left: -5px; top: 8px; height: 10px; 
                        border-left: 1px solid #707070;
                    }
                    a{ font-size: 12px; color: #151515;}
                }
                li:first-child::before{ border-left: 0;}
            }
            .f_info{
                font-size: 12px; line-height: 1.5;
                span{ 
                    margin-right: 10px; border: 0;
                }
                .bar{ 
                    padding-left: 10px; position: relative;
                    &::before{
                        content: ""; position: absolute; left: 0; top: 4px;
                        border-left: 1px solid #707070; opacity: .6;
                        width: 1px; height: 10px;
                    }
                }
            }
            p{ font-size: 12px; margin-top: 4px; display: block;}
        }    
    }
    .article_award{
        margin: 50px auto 0; padding: 20px 0; background: #f5f5f5;
        .inner{
            width: 980px; margin: 0 auto;
            ul{ display: flex; justify-content: space-between;}
        }
    }
}